import React from 'react';
import PropTypes from 'prop-types';
import csx from 'classnames';
import { ABSENT, CORRECT, PRESENT, UNUSED } from './constants.js';
import css from './Keyboard.css';

// ⏎ ⌫

const stateToClass = {
  [ABSENT]: css.absent,
  [CORRECT]: css.correct,
  [PRESENT]: css.present,
  [UNUSED]: css.unused
};

export default class KeyboardButton extends React.PureComponent {
  static propTypes = {
    value: PropTypes.string,
    state: PropTypes.number,
    onClick: PropTypes.func
  };

  constructor (props) {
    super(props);
    this.state = {};
  }

  onClick = () => {
    this.props.onClick?.(this.props.value);
  };

  render () {
    const { value, state } = this.props;
    return (
      <button
        type="button"
        className={csx(css.key, stateToClass[state])}
        onClick={this.onClick}
        >
        {value.toUpperCase()}
      </button>
    );
  }
}
